import React from "react";
import { formatDate } from "components/utils/utils";
import Table from "components/ui/Table";
import queryClient from "components/utils/queryClient";
import { usePageContext } from "components/pages/Page";
import { JobError } from "types";

export default function JobErrorTable() {
  const { resource } = usePageContext();
  const columns = [
    {
      title: "Timestamp",
      dataIndex: "timestamp",
      key: "timestamp",
      render: (text) => formatDate(text),
    },
    {
      title: "Message",
      dataIndex: "message",
      key: "message",
    },
  ];

  return (
    <Table
      data={queryClient.getQueryData<JobError[]>(`/job/${resource?.id}/errors`)}
      columns={columns}
    />
  );
}
